    public getAddToSEETeamsInfo = async (oid: number, title: string, token: string, type?: string): Promise<AddToSEETeamsRef> => {
        const myReturnList: AddToSEETeamsRef = { OID: oid, Title: title };
        const headers1 = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}` });
        const options = {
            headers: headers1
        };
        let azureUrl = Settings.AppSettings.addToSEETeamURL;
        if (oid && oid > 0 && type != undefined) {
            if (type == "offer")
                azureUrl = azureUrl + "?OID=" + oid + "&IPKitID=undefined&DocumentURL=undefined&ProjectsiteId";
            else if (type == "ipkit")
                azureUrl = azureUrl + "?OID=undefined&IPKitID=" + oid + "&DocumentURL=undefined&ProjectsiteId";
        }
        else
            azureUrl = azureUrl + "?OID=-1&IPKitID=undefined&DocumentURL=undefined&ProjectsiteId";
        const response = await fetch(azureUrl, options);
        const body = await response.json();
        const data: AddToSEETeams[] = [];
        if (body && body.length > 0) {
            body.forEach((item, index) => {
                if (item && item["$id"] && item["$id"].length > 0) {

                    let addToSEETeamObj: AddToSEETeams = {
                        ID: item["$id"], ProjectSiteID: item.ProjectSiteID
                        , Title: item.Title, ProjectSiteUrl: item.ProjectSiteUrl
                        , GroupId: item.GroupId, Name: item.Title
                        , IsExist: item.IsExist, SeeTeamsNotification: item.SeeTeamsNotification
                    }
                    if (item.channelsName && item.channelsName.length > 0) {
                        addToSEETeamObj.Channel = [];
                        item.channelsName.forEach((o, i) => {
                            if (o && o.toLowerCase() != "general") {
                                addToSEETeamObj.Channel && addToSEETeamObj.Channel.push({
                                    ID: (i + 1)
                                    , Title: o
                                    , ChannelName: o
                                    , IsAdded: false
                                })
                            }
                        });
                    }
                    data.push(addToSEETeamObj);
                    myReturnList.AddToSEETeamsData = data;
                }
            });
        }
        return myReturnList;

    }

