        const productList = returnList.RefinerTwo;
        const offerResult = returnList.OfferResult;
        const offerCount = returnList.OfferCount;
        const ipkitCount = returnList.IPkitCount;
        const docsCount = returnList.DocsCount;
        const virtuosoCount = returnList.VirtuosoCount;
        const compassOneCount = returnList.CompassOneCount;
        const docascodeCount = returnList.DocAsCodeCount;
        const projectCount = returnList.ProjectCount;
        const serviceCodeCount = returnList.ServiceCodeCount;

