    //to get the tile control
    public getTileViewControl(offerRes: IOfferData[]) {
        let custumControl;
        if (this.state.ssuxType == SSUXType.offer)
            custumControl = <CustumTileView offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView, type) => this.onAddToSEETeamClick(e, selectedData, showView, type)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        else if (this.state.ssuxType == SSUXType.ipkit)
            custumControl = <CustumTileViewIpkit offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView, type) => this.onAddToSEETeamClick(e, selectedData, showView, type)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        else if (this.state.ssuxType == SSUXType.docs)
            custumControl = <CustumTileViewDocs offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        else if (this.state.ssuxType == SSUXType.virtuoso) {
            custumControl = <CustumTileViewVirtuoso offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        } else if (this.state.ssuxType == SSUXType.compassOne) {
            custumControl = <CustumTileViewCompassOne offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        } else if (this.state.ssuxType == SSUXType.docascode) {
            custumControl = <CustumTileViewDocasCode offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        } else if (this.state.ssuxType == SSUXType.project) {
            custumControl = <CustumTileViewCampusSeeTeams offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        } else if (this.state.ssuxType == SSUXType.serviceCode) {
            custumControl = <CustumTileViewServiceasCode offerResult={offerRes}
                onAddToSEETeamClick={(e, selectedData, showView) => this.onAddToSEETeamClick(e, selectedData, showView)}
                pageSize={this.state.pageSize} pageNo={this.state.pageNo} />
        }
        else
            custumControl = null;
        return custumControl;
    }
    //to get the filter panel class
    public getFilterPanelClass(domainList: IDataSource, productList: IDataSource) {
        if (this.state.searchText != undefined && this.state.searchText.length > 0
            && ((productList && productList.data
                && productList.data.find(o => o.selected == true) != undefined) ||
                (productList && productList.data &&
                    domainList.data.find(o => o.selected == true) != undefined)))
            return "filterIcon";
        else if (this.state.searchText != undefined && this.state.searchText.length > 0
            && (productList && productList.data && productList.data.length > 0 ||
                domainList && domainList.data && domainList.data.length > 0))
            return "filterIcon noFilterSelection";
        else
            return "filterIcon disableFilter";
    }
