    public updateList(productList: IDDLData[], selectedData: IData[]) {
        const dataSource = [...productList];
        if (dataSource && dataSource.length > 0) {
            dataSource.forEach(obj => { obj.selected = false; });
        }
        if (selectedData && selectedData.length > 0) {
            selectedData.forEach(obj => {
                const currItem = dataSource.find(item => item.text === obj.text && item.value === obj.value);
                if (currItem) {
                    currItem.selected = true;
                }
            });
        }
        productList = dataSource;
        return productList;
    }
